<template>
    <van-list
        v-model="loading"
        :finished="finished"
        @load="updateList"
    >
        <table>
            <thead><tr>
                <th>好友姓名</th>
                <th>状态</th>
                <th>激活时间</th>
            </tr></thead>
            <tbody><tr
                v-for="(item, i) in list"
                :key="i">
                <td class="name"><img :src="item.headIconUrl">{{ item.nickname }}</td>
                <td>{{ item.status | statusFormat }}</td>
                <td>{{ item.activeTime | dateFormat }}</td>
            </tr></tbody>
        </table>
    </van-list>
</template>

<script>
import { getFriendsList } from '@/api/partner';
import { format } from '@/utils/time';

export default {
    data() {
        return {
            list: [],
            finished: false,
            loading: false,
            page: 1,
        };
    },
    methods: {
        format,
        updateList() {
            this.loading = true;
            getFriendsList({ page: this.page }).then((ret) => {
                this.list.push(...ret.list);
                this.finished = ret.endMark;
                this.loading = false;
                this.page += 1;
            }).catch(() => {
                this.$error();
            });
        },
    },
    filters: {
        dateFormat(value) {
            return format(value, 'YYYY.MM.DD hh:mm');
        },
        statusFormat(value) {
            return value === 1 ? '邀请成功' : '邀请中';
        },
    },
};
</script>

