<script>
import { doLogin, toast } from '@/utils/jsApiV2/client';
import { getWebviewDeeplink } from '@/utils/jsApiV2/deeplink';
import { isXiangkan, isIOS } from '@/utils/ua';
import { deeplinkApp } from '@/utils/share';
import GrayMask from '@/components/GrayMask';
import config from './config';

export default {
    props: {
        isLogin: {
            type: Boolean,
            default: false,
        },
        inBlacklist: {
            type: Boolean,
            default: false,
        },
    },
    components: {
        GrayMask,
    },
    data() {
        return {
            config,
        };
    },
    computed: {
        isShowToApp() {
            return !isXiangkan() && !isIOS();
        },
    },
    mounted() {
        this.$o2o({
            e_c: '首页曝光',
            e_a: '曝光',
            e_n: '地推合伙人平台',
        });
    },
    methods: {
        goJoin() {
            if (this.isLogin) {
                if (this.inBlacklist) {
                    toast('当前账号异常，无法申请合伙人');
                    return;
                }
                this.$o2o({
                    e_c: '申请合伙人',
                    e_a: '点击',
                    e_n: '地推合伙人平台',
                });
                this.$router.push('/act/partner/join');
            } else {
                doLogin('location.reload');
            }
        },
        gotoApp() {
            deeplinkApp(getWebviewDeeplink(window.location.href), () => {}, 'CK1428516045320');
        },
    },
};
</script>

