<template>
    <div class="partner">
        <partner-poster
            v-if="!isPartner"
            :is-login="isLogin"
            :in-blacklist='inBlacklist'
        />
        <template v-else>
            <poster-maker :user="user"/>
            <div
                class="income-summary"
                @click="goIncome">
                <div class="income-title">
                    <div class="myrecord">
                        我的战绩
                    </div>
                    <div class="benefits">
                        全部收益
                    </div>
                </div>
                <div class="income">
                    <div>
                        <h3>今日邀请（个）</h3>
                        <span>{{ invitedInToday }}</span>
                    </div>
                    <i class="v-hr"/>
                    <div>
                        <h3>昨日收入</h3>
                        <span>{{ earnedYesterday }}</span><i>元</i>
                    </div>
                </div>
            </div>
            <act-section
                title="邀请列表"
                class="friend-list">
                <friends-list />
            </act-section>
            <act-section
                title="任务说明"
                class="taskList">
                <task-rule @play="playVido(video)" />
            </act-section>
        </template>
        <GrayMask
            :show="openPartnerModal && !showVideo">
            <div
                v-if='openCongratulateModal'
                class="congratulate-partner center">
                <div class="congratulate-partner-content">
                    <div class="congratulate-partner-content-rank">
                        第 <span>{{ $route.query.inviteNo }}</span> 位
                    </div>
                    <div
                        @click="getFastMoney"
                        class="open-partner-modal-button">
                        学习教程快速赚钱
                    </div>
                </div>
            </div>
            <div
                v-else
                class="learn-skill center">
                <div class="learn-skill-content">
                    <div class="learn-skill-content-title">
                        <p>学习邀请秘诀</p>
                        <p >提升200%成功率</p>
                    </div>
                    <div class="learn-skill-video">
                        <img
                            @click="setVideo"
                            :src="video.src" >
                    </div>
                    <div
                        @click="setVideo"
                        class="open-partner-modal-button">
                        开启梦想之旅
                    </div>
                </div>
            </div>
        </GrayMask>
        <div
            class="videoContainer"
            v-if="showVideo"
        >
            <p
                @click="closeVideo"
                class='getback'>关闭</p>
            <mi-video
                :video-url="video.url"
                :poster="video.poster"
                :duration="video.duration || 0"
                :c-s-style="video.CSStyle"
                @ended="closeVideo"
            />
        </div>
    </div>
</template>

<script>
import {
    isTakeOverBackByWeb,
    openCommonWebViewByPath,
} from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { getActInfo } from '@/api/partner';
import GrayMask from '@/components/GrayMask';
import MiVideo from '@/components/MiVideo';
import PartnerPoster from './PartnerPoster';
import PosterMaker from './PosterMaker';
import ActSection from './Section';
import TaskRule from './TaskRule';
import FriendsList from './FriendsList';
import config from './config';

export default {
    name: 'Partner',
    components: {
        PartnerPoster,
        PosterMaker,
        ActSection,
        TaskRule,
        FriendsList,
        GrayMask,
        MiVideo,
    },
    data() {
        return {
            user: {},
            earnedYesterday: 0,
            invitedInToday: 0,
            isPartner: false,
            openPartnerModal: false,
            openCongratulateModal: true,
            video: config.video,
            showVideo: false,
            isLogin: false,
            isLoading: true,
            inBlacklist: false,
        };
    },
    watch: {
        showVideo(v) {
            isTakeOverBackByWeb(v);
            if (v) {
                addCustomEventListener('backKey', this.backKeyHandler);
            } else {
                removeCustomEventListener('backKey', this.backKeyHandler);
            }
        },
    },
    created() {
        getActInfo().then((ret) => {
            this.isPartner = ret.isPartner;
            this.inBlacklist = ret.inBlacklist;
            if (this.isPartner) {
                this.$o2o({
                    e_c: '首页曝光',
                    e_a: '曝光',
                    e_n: '地推合伙人平台首页',
                });
            }
            this.earnedYesterday = ret.earnedYesterday;
            this.invitedInToday = ret.invitedInToday;
            this.user = {
                headIconUrl: ret.headIconUrl,
                invitationCode: ret.invitationCode,
                partnerCode: ret.partnerCode,
                nickname: ret.nickname,
            };
            this.isLogin = true;
            this.$root.hidePlaceholder();
        }).catch((e) => {
            if (e.response && e.response.status === 401) {
                this.isLogin = false;
                this.$root.hidePlaceholder();
            } else {
                this.$error();
            }
        });
        this.checkModal();
    },
    methods: {
        checkModal() {
            if (this.$route.query.inviteNo) {
                this.openPartnerModal = true;
                isTakeOverBackByWeb(true);
                this.$o2o({
                    e_c: '弹框引导1',
                    e_a: '曝光',
                    e_n: '地推合伙人平台弹窗',
                });
            }
        },
        playVido(video) {
            this.setVideo();
            this.video = { ...this.video, video };
        },
        closePartnerModal() {
            this.openPartnerModal = false;
        },
        getFastMoney() {
            this.openCongratulateModal = false;
            this.$o2o({
                e_c: '弹框引导1',
                e_a: '点击',
                e_n: '地推合伙人平台弹框',
            });
            this.$o2o({
                e_c: '弹框引导2',
                e_a: '曝光',
                e_n: '地推合伙人平台弹框',
            });
        },
        setVideo() {
            this.closePartnerModal();
            this.$o2o({
                e_c: '弹框引导2',
                e_a: '点击',
                e_n: '地推合伙人平台弹框',
            });
            this.showVideo = true;
        },
        closeVideo() {
            this.showVideo = false;
        },
        goIncome() {
            this.$o2o({
                e_c: '我的收益',
                e_a: '点击',
                e_n: '地推合伙人平台首页',
            });
            openCommonWebViewByPath('/mobile-v2/act/partner/income');
        },
        backKeyHandler() {
            this.closeVideo();
        },
    },
};
</script>

