<template>
    <div class="maker">
        <div class="subtitle">线下邀好友扫码下载APP</div>
        <div class="red">
            <div class="head-icon">
                <img :src="user.headIconUrl || config.default.userIcon">
            </div>
            <p class="no">地推合伙人{{ user.partnerCode }}</p>
            <p class="code">我的专属邀请码：{{ user.invitationCode }}</p>
            <div class="qr-box">
                <canvas
                    class="qr"
                    ref="partner_qr"/>
            </div>
            <p class="footer">新用户微信扫描立领8元奖励!</p>
            <p class="below-more"/>
        </div>
        <div
            class="btn"
            @click="createPoster">领取我的专属地推海报</div>
        <NeedUpdate
            v-if="needUpdate"
            @close='closeNeedUpdateModal'/>
    </div>
</template>

<script>
import QRious from 'qrious';
import config from '@/config';
import { shareGroundPromotionPoster } from '@/utils/jsApiV2/act';
import { getShareUrl } from '@/utils/share';
import NeedUpdate from './needUpdate';

const SHARE_BG = 'https://ts.market.mi-img.com/download/Browser/0186e45e92d37914443cab5caac488df7ce4067f4/a.webp';
const SHARE_TIP = '微信扫码立即下载';
const LOWEST_VERSION = 40800;

export default {
    props: {
        user: {
            required: true,
            type: Object,
        },
    },
    components: {
        NeedUpdate,
    },
    data() {
        return {
            config,
            needUpdate: false,
        };
    },
    watch: {
        user: {
            handler(v) {
                if (v.partnerCode) {
                    const url = `https://feed.browser.miui.com/mobile-v2/act/invitation/share?shareId=${encodeURIComponent(v.invitationCode)}&share=partner`;
                    getShareUrl(url).then((ret) => {
                        this.qr.value = ret;
                    }).catch(() => {
                        this.qr.value = url;
                    });
                }
            },
            immediate: true,
        },
    },
    methods: {
        closeNeedUpdateModal() {
            this.needUpdate = false;
        },
        createPoster() {
            this.$o2o({
                e_c: '专属海报',
                e_a: '点击',
                e_n: '地推合伙人平台首页',
            });
            if (config.versionCode < LOWEST_VERSION) {
                this.needUpdate = true;
                return;
            }
            shareGroundPromotionPoster(
                SHARE_BG,
                `地推合伙人${this.user.partnerCode}`,
                `我的专属邀请码：${this.user.invitationCode}`,
                this.qr.value,
                SHARE_TIP,
            );
        },
    },
    mounted() {
        this.qr = new QRious({ // eslint-disable-line
            element: this.$refs.partner_qr,
        });
    },
};
</script>

